.header {
    color: #fff;
    padding: 0 100px;
    height: 90px;
    line-height: 90px;
    .logo {
        img {
            width: 40px;
            height: 40px;
            margin-right: 10px;
        }
        span {
            font-size: 20px;
        }
    }
    .launchBtn {
        background: #FDFF00;
        color: #0A0A0A;
        font-size: 15px;
        line-height: 50px;
        padding: 0 30px;
    }
}

@media (max-width: 960px) {
    .header {
        color: #fff;
        padding: 0 15px;
        height: 50px;
        line-height: 50px;
        .logo {
            img {
                width: 40px;
                height: 40px;
                margin-right: 5px;
            }
            span {
                font-size: 14px;
            }
        }
        .launchBtn {
            font-size: 13px;
            line-height: 30px;
            padding: 0 15px;
        }
    }
}