.home {
    padding: 0 100px;
    color: #fff;
    .nav {
        position: relative;
        text-align: right;
        .outer {
            text-align: left;
            position: absolute;
            left: 0;
            top: 150px;
            .title {
                color: #fdff00;
                font-weight: bold;
                font-size: 20px;
                margin-bottom: 40px;
            }
            .subTitle {
                font-weight: 600;
                font-size: 120px;
                margin-bottom: 50px;
            }
            .desc {
                width: 550px;
                color: #818181;
                font-size: 20px;
                line-height: 1.6;
            }
        }
    }
    .show {
        padding: 0 100px;
        .item {
            img {
                width: 90px;
                height: 52px;
            }
            .desc {
                font-weight: 600;
                font-size: 23px;
                color: #818181;
                margin-top: 30px;
            }
        }
    }
    .info {
        margin-top: 120px;
        .title {
            font-weight: bold;
            font-size: 85px;
            text-align: center;
            margin-bottom: 60px;
            .s {
                color: #fdff00;
            }
        }
        .top {
            .side,
            .right {
                width: 50%;
                box-sizing: border-box;
            }
            .side {
                padding: 0 90px;
                .title,
                .subtitle {
                    text-align: left;
                    font-size: 65px;
                    font-weight: bold;
                }
                .title {
                    margin-bottom: 20px;
                }
                .desc {
                    color: #8a8a8a;
                    font-size: 20px;
                    margin-top: 30px;
                    line-height: 1.6;
                }
            }
            .right {
                position: relative;
                padding: 80px 0 140px;
                .card1 {
                    position: relative;
                    width: 320px;
                    height: auto;
                    z-index: 9;
                }
                .card2,
                .card3 {
                    position: absolute;
                    right: 170px;
                    height: auto;
                }
                .card2 {
                    width: 200px;
                    top: 0;
                }
                .card3 {
                    width: 240px;
                    bottom: 0;
                }
            }
        }
        .box {
            margin-top: 50px;
            .left,
            .side {
                width: 50%;
                box-sizing: border-box;
            }
            .left {
                img {
                    width: 100%;
                }
            }
            .side {
                padding: 0 90px;
                .title,
                .subtitle {
                    text-align: left;
                    font-size: 65px;
                    font-weight: bold;
                }
                .title {
                    margin-bottom: 20px;
                }
                .subtitle {
                    color: #fdff00;
                }
                .desc {
                    color: #8a8a8a;
                    font-size: 20px;
                    margin-top: 30px;
                    line-height: 1.6;
                }
            }
        }
    }
}

@media (max-width: 960px) {
    .home {
        padding: 0 15px;
        .nav {
            .bg {
                width: 100%;
            }
            .outer {
                top: 30px;
                .title {
                    font-size: 13px;
                    margin-bottom: 20px;
                }
                .subTitle {
                    font-size: 30px;
                    margin-bottom: 30px;
                }
                .desc {
                    width: 100%;
                    font-size: 13px;
                    color: #fff;
                }
            }
        }
        .show {
            padding: 0;
            .item {
                text-align: center;
                img {
                    width: 40px;
                    height: 24px;
                }
                .desc {
                    font-size: 12px;
                    margin-top: 30px;
                }
            }
        }
        .info {
            margin-top: 50px;
            .title {
                font-size: 32px;
                margin-bottom: 50px;
                .s {
                    color: #fdff00;
                }
            }
            .top {
                display: block;
                .side,
                .right {
                    width: 100%;
                }
                .side {
                    padding: 0 15px;
                    margin-bottom: 30px;
                    .title,
                    .subtitle {
                        font-size: 24px;
                    }
                    .title {
                        margin-bottom: 10px;
                    }
                    .desc {
                        font-size: 13px;
                    }
                }
                .right {
                    position: relative;
                    padding: 80px 0 140px;
                    .card1 {
                        width: 200px;
                    }
                    .card2,
                    .card3 {
                        right: 15px;
                    }
                    .card2 {
                        width: 160px;
                    }
                    .card3 {
                        width: 180px;
                    }
                }
            }
            .box {
                margin-top: 50px;
                display: block;
                .left,
                .side {
                    width: 100%;
                }
                .left {
                    img {
                        width: 100%;
                    }
                }
                .side {
                    margin-top: 30px;
                    padding: 0 15px;
                    .title,
                    .subtitle {
                        font-size: 24px;
                    }
                    .title {
                        margin-bottom: 20px;
                    }
                    .desc {
                        font-size: 13px;
                    }
                }
            }
        }
    }
}
